import { Component } from '@angular/core';

import { User } from '../_models';
import { AccountService } from '../_services';

import { HttpClient } from '@angular/common/http';

@Component({ templateUrl: 'home.component.html' })
export class HomeComponent {
    user: User;
    fromRest: string;
    url = 'http://shira-jaxrs-docker-example-git-vachub.apps.us-west-1.starter.openshift-online.com/';

    constructor(private accountService: AccountService, private httpClient: HttpClient) {
        this.user = this.accountService.userValue;
    }

    public showGetRest() {
      this.getRest().subscribe(result => this.fromRest = result);
    }

    private getRest() {
      return this.httpClient
        .get(this.url + 'vacation', {responseType: 'text'});
    }

    public showPostRest() {
      this.postRest().subscribe(result => this.fromRest = result as string);
    }

    private postRest() {
      return this.httpClient
        .post(this.url + 'vacation', {id: this.user.id + '-1', title: 'my vac', description: 'a great vac', type: 'cruise'});
    }
}
