export interface Vacation {
    id: string; // user id (email) - uuid
    title: string;
    description: string;
    type: VacationType; // e.g. cruise
}

export enum VacationType {
    Cruise = 'Cruise',
    Travel = 'Travel',
    Sports = 'Sports',
    Shopping = 'Shopping',
    Flight = 'Flight',
    Business = 'Business',
    Virtual = 'Virtual',
    Other = 'Other'
}
